.footer{
    background: #232323 0% 0% no-repeat padding-box;
    width: 100%;
    height: 300px;
}

.group1{
    display: flex;
    padding: 60px;
    margin: auto 50px;
}

.group1 .group1-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
}
.group1 .group1-logo img{
    width: 160px;
}

.group1 .group1-link{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
}

.group1 .group1-link a{
    text-decoration: none;
    padding: 20px;
    margin: 0 50px;
    font: normal normal bold 15px/17px Helvetica;
    letter-spacing: 0px;
    color: #FAFAFA;
}

.group2 small{
    font: normal normal bold 15px/17px Helvetica;
    letter-spacing: 0px;
    color: #FAFAFA;
}